export function getLocationSearch() {
    var url = window.location.search; //获取url中"?"符后的字串
    var theRequest = {};
    if (url.indexOf("?") != -1) {
        var str = url.substr(1);
        var strs = str.split("&");
        for (var i = 0; i < strs.length; i++) {
            theRequest[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
        }
    }
    return theRequest;
};

// min timestamp
export function formatMinTimeTips(time, second) {
    let result = '';

    //
    if (time > -1) {
        const hour = Math.floor(time / 60) % 60;
        let min = time % 60;

        min = Math.round(min);

        if (hour < 10) {
            result = '0' + hour + ":";
        } else {
            result = hour + ":";
        }

        if (min < 10) {
            result += "0";
        }
        result += min;

        if (!isEmpty(second)) {
            if (second < 10) {
                second = '0' + second;
            }
            result += (':' + second);
        }
    }

    return result;
}

// second timestamp
export function formatSecondTimeTips(time) {
    let result = '';
    if (time > -1) {
        const hour = Math.floor(time / 60 / 60) % 60;
        let min = Math.floor(time / 60) % 60;
        let second = time % 60

        min = Math.round(min);

        if (hour < 10) {
            result = '0' + hour + ":";
        } else {
            result = hour + ":";
        }

        if (min < 10) {
            result += "0";
        }
        result += min + ':';

        if (second < 10) {
            result += '0'
        }
        result += second
    }

    return result;
}


export function formatSecondTime(time) {
    let result = {};
    if (time > -1) {
        const hour = Math.floor(time / 60 / 60) % 60;
        let min = Math.floor(time / 60) % 60;
        let second = time % 60
        result = {
            hour,
            min,
            second
        }
    }

    return result;
}

/**
 *
 * @param time
 */
export function formatMinuteTimestamp(day, time) {
    const hour = Math.floor(time / 60) % 60;
    const min = Math.floor(time % 60);
    const nowMinuteTimestamp = new Date(day).setHours(
        hour,
        min,
        0,
        0
    );
    return nowMinuteTimestamp;
}

export function formatSecondTimestamp(day, time) {
    const hour = Math.floor(time / 60 / 60) % 60;
    const min = Math.floor(time / 60) % 60;
    const second = time % 60
    const nowSecondTimestamp = new Date(day).setHours(
        hour,
        min,
        second,
        0
    );
    return nowSecondTimestamp;
}

