import Player from './player';
import Events from "./utils/events";
import {
    DECODE_TYPE,
    DEFAULT_JESSIBUCA_OPTIONS,
    DEFAULT_PLAYBACK_FORWARD_MAX_RATE_DECODE_IFRAME,
    DEMUX_TYPE,
    EVENTS,
    EVENTS_ERROR,
    JESSIBUCA_EVENTS,
    PLAY_TYPE,
    PLAYER_PLAY_PROTOCOL, PLAYER_STATUS,
    SCALE_MODE_TYPE
} from "./constant";
import {clamp, getStrLength, isEmpty, isNotEmpty, supportWCS, uuid16} from "./utils";
import Emitter from "./utils/emitter";
import Debug from "./utils/debug";


class JessibucaPro extends Emitter {
    // static ERROR = EVENTS_ERROR

    // static TIMEOUT = {
    //     loadingTimeout: EVENTS.loadingTimeout,
    //     delayTimeout: EVENTS.delayTimeout,
    // }

    constructor(options) {
        super()
        let _opt = Object.assign({}, DEFAULT_JESSIBUCA_OPTIONS, options);
        let $container = options.container;
        if (typeof options.container === 'string') {
            $container = document.querySelector(options.container);
        }
        if (!$container) {
            throw new Error('Jessibuca need container option');
            return;
        }

        // check container node name
        if ($container.nodeName === 'CANVAS' || $container.nodeName === 'VIDEO') {
            throw new Error(`Jessibuca container type can not be ${$container.nodeName} type`);
            return;
        }

        $container.classList.add('jessibuca-container');

        if (_opt.isLive === false) {
            const $videoElement = document.createElement('video');
            $videoElement.muted = true;
            $videoElement.setAttribute("controlsList", "nodownload");
            $videoElement.disablePictureInPicture = 'disablePictureInPicture';
            $videoElement.style.position = "absolute";
            $videoElement.style.top = 0;
            $videoElement.style.left = 0;
            $videoElement.style.height = "100%";
            $videoElement.style.width = '100%';
            $container.appendChild($videoElement);
            this.$videoElement = $videoElement;
            this.$container = $container;
            this._opt = _opt;
            return;
        }

        delete _opt.container;

        // s -> ms
        if (isNotEmpty(_opt.videoBuffer)) {
            _opt.videoBuffer = Number(_opt.videoBuffer) * 1000
        }

        // s -> ms
        if (isNotEmpty(_opt.videoBufferDelay)) {
            _opt.videoBufferDelay = Number(_opt.videoBufferDelay) * 1000
        }

        // setting
        if (isNotEmpty(_opt.timeout)) {
            if (isEmpty(_opt.loadingTimeout)) {
                _opt.loadingTimeout = _opt.timeout;
            }

            if (isEmpty(_opt.heartTimeout)) {
                _opt.heartTimeout = _opt.timeout
            }
        }

        this._opt = _opt;
        this.$container = $container;
        this._loadingTimeoutReplayTimes = 0;
        this._heartTimeoutReplayTimes = 0;
        this.events = new Events(this);
        this.debug = new Debug(this);
        this._initPlayer($container, _opt);
    }

    /**
     *
     */
    destroy() {
        if (this.$videoElement) {
            this.$videoElement.pause();
            this.$videoElement.currentTime = 0;
            this.$videoElement.src = '';
            this.$container.removeChild(this.$videoElement);
            this.$videoElement = null;
        }
        if (this.events) {
            this.events.destroy();
            this.events = null;
        }

        if (this.player) {
            this.player.destroy();
            this.player = null;
        }

        this.$container = null;

        this._resetOpt();
        this._loadingTimeoutReplayTimes = 0;
        this._heartTimeoutReplayTimes = 0;
        this.off();
        this.debug && this.debug.log('jessibuca', 'destroy')
    }

    _resetOpt() {
        this._opt = DEFAULT_JESSIBUCA_OPTIONS;
    }

    _initPlayer($container, options) {
        this.player = new Player($container, options);
        this.debug.log('jessibuca', '_initPlayer', this.player.getOption())
        this._bindEvents();
    }

    _resetPlayer(options = {}) {
        if (this.player) {
            this.player.destroy();
            this.player = null;
        }
        this._opt = Object.assign(this._opt, options);
        this._opt.url = '';// reset url
        this._initPlayer(this.$container, this._opt);
    }

    _bindEvents() {
        // 对外的事件
        Object.keys(JESSIBUCA_EVENTS).forEach((key) => {
            this.player.on(JESSIBUCA_EVENTS[key], (value) => {
                this.emit(key, value)
            })
        })

        this.player.once(EVENTS.beforeDestroy, () => {
            this.emit(EVENTS.close);
            this.destroy();
        })
    }

    /**
     *
     * @returns {{}|({rotate: number, isResize: boolean, playbackForwardMaxRateDecodeIFrame: number, loadingTimeoutReplayTimes: number, wasmDecodeErrorReplay: boolean, demuxType: string, useWCS: boolean, useOffscreen: boolean, loadingTimeout: number, playbackConfig: {playList: [], fps: string}, timeout: number, wasmUseVideoRender: boolean, heartTimeoutReplay: boolean, isNotMute: boolean, protocol: number, useMSE: boolean, operateBtns: {play: boolean, fullscreen: boolean, record: boolean, screenshot: boolean, audio: boolean}, isHls: boolean, hotKey: boolean, heartTimeout: number, isFlv: boolean, openWebglAlignment: boolean, hasVideo: boolean, loadingTimeoutReplay: boolean, hasAudio: boolean, debug: boolean, loadingText: string, useWasm: boolean, keepScreenOn: boolean, isFullResize: boolean, watermarkConfig: {}, forceNoOffscreen: boolean, videoBuffer: number, decoder: string, isWebrtc: boolean, url: string, showBandwidth: boolean, hiddenAutoPause: boolean, autoWasm: boolean, heartTimeoutReplayTimes: number, playType: string, background: string, hasControl: boolean, controlAutoHide: boolean, playbackDelayTime: number, playbackFps: number, supportDblclickFullscreen: boolean, wcsUseVideoRender: boolean} & *)}
     */
    getOption() {
        if (this.player) {
            return this.player.getOption();
        }

        return {}
    }

    /**
     * 是否开启控制台调试打印
     * @param value {Boolean}
     */
    setDebug(value) {
        this.player && this.player.updateOption({
            debug: !!value
        })
    }

    /**
     *
     */
    mute() {
        this.player && this.player.mute(true);
    }

    /**
     *
     */
    cancelMute() {
        this.player && this.player.mute(false);
    }

    /**
     *
     * @param value {number}
     */
    setVolume(value) {
        this.player && (this.player.volume = value);
    }

    /**
     *
     */
    audioResume() {
        this.player && this.player.audio && this.player.audio.audioEnabled(true);
    }

    /**
     * 设置超时时长, 单位秒 在连接成功之前和播放中途,如果超过设定时长无数据返回,则回调timeout事件
     * @param value {number}
     */
    setTimeout(time) {
        time = Number(time);
        this.player && this.player.updateOption({
            timeout: time,
            loadingTimeout: time,
            heartTimeout: time
        })
    }

    /**
     *
     * @param type {number}: 0,1,2
     */
    setScaleMode(type) {
        type = Number(type);
        let options = {
            isFullResize: false,
            isResize: false
        }
        switch (type) {
            case SCALE_MODE_TYPE.full:
                options.isFullResize = false;
                options.isResize = false;
                break;
            case SCALE_MODE_TYPE.auto:
                options.isFullResize = false;
                options.isResize = true;
                break;
            case SCALE_MODE_TYPE.fullAuto:
                options.isFullResize = true;
                options.isResize = true;
                break;
        }
        if (this.player) {
            this.player.updateOption(options);
            this.resize();
        }
    }

    /**
     *
     * @returns {Promise<commander.ParseOptionsResult.unknown>}
     */
    pause() {
        return this.player && this.player.pause();
    }

    /**
     *
     */
    close() {
        // clear url
        this._resetOpt();
        return this.player && this.player.close();
    }


    /**
     *
     */
    clearView() {
        this.player && this.player.video && this.player.video.clearView()
    }

    /**
     *
     * @param url {string}
     * @returns {Promise<unknown>}
     */
    play(url, options = {}) {
        return new Promise((resolve, reject) => {
            if (!url && !this._opt.url) {
                this.emit(EVENTS.error, EVENTS_ERROR.playError)
                reject();
                return;
            }

            if (this._opt.isLive === false) {
                this.$videoElement.controls = 'controls';
                this.$videoElement.muted = false
                this.$videoElement.src = url
                this.$videoElement.play();
                resolve(this.$videoElement);
                return;
            }


            if (this.player) {
                if (url) {
                    // url 相等的时候。
                    if (this._opt.url) {
                        // 存在相同的 url
                        if (url === this._opt.url) {
                            // 正在播放
                            if (this.player.playing) {
                                resolve();
                            } else {
                                // pause ->  play
                                this.clearView();
                                this.player.play(this._opt.url, this._opt.playOptions).then(() => {
                                    resolve();
                                    // 恢复下之前的音量
                                    this.player.resumeAudioAfterPause();
                                }).catch((e) => {
                                    this.player.debug.error('Jessibuca', 'this.player.play error', e)
                                    this.player.pause().then(() => {
                                        reject();
                                    })
                                })
                            }
                        } else {
                            // url 发生改变了
                            this.player.pause().then(() => {
                                // 清除 画面
                                this.clearView();
                                return this._play(url, options);
                            }).catch((e) => {
                                this.player.debug.error('Jessibuca', 'this.player.pause error', e)
                                reject();
                            })
                        }
                    } else {
                        return this._play(url, options);
                    }
                } else {
                    //  url 不存在的时候
                    //  就是从 play -> pause -> play
                    this.player.play(this._opt.url, this._opt.playOptions).then(() => {
                        resolve();
                        // 恢复下之前的音量
                        this.player.resumeAudioAfterPause();
                    }).catch((e) => {
                        this.player.debug.error('Jessibuca', 'this.player.play error', e)
                        this.player.pause().then(() => {
                            reject();
                        })
                    })
                }
            } else {
                if (url) {
                    return this._play(url);
                } else {
                    return this._play(this._opt.url);
                }
            }
        })
    }

    /**
     * 录像回放
     * @param url {string}
     */
    playback(url, options = {}) {
        if (this._opt.isLive === false) {
            return Promise.reject();
        }

        this._resetPlayer({
            videoBuffer: 0,// 录播的时候不缓存。
            playbackConfig: options,
            showPlaybackControl: options.showControl === true ? true : false,
            playType: PLAY_TYPE.playbackTF,
            useMSE: false, //
            useWCS: false //
        });
        return this.play(url);
    }

    playbackPause() {

    }

    playbackPause() {

    }


    /**
     * 快放 1倍，2倍，4倍，8倍,支持范围 0.1 - 8
     * @param rate
     * @returns {Promise<unknown>}
     */
    forward(rate) {
        if (this._opt.isLive === false || this._opt.playType === PLAY_TYPE.player) {
            return Promise.reject();
        }

        return new Promise((resolve, reject) => {
            if (this.player.playing) {
                rate = clamp(Number(rate), 0.1, 8);
                this.player.playback.setRate(rate);
                this.player.video && this.player.video.setRate(rate);
                this.player.audio && this.player.audio.setRate(rate);
                this.player.decoderWorker.clearWorkBuffer(true);
                this.player.decoderWorker && this.player.decoderWorker.updateWorkConfig({
                    key: 'playbackRate',
                    value: rate
                })
                resolve()
            } else {
                reject();
            }
        })
    }

    /**
     * 快放->恢复
     * @returns {Promise<unknown>}
     */
    normal() {
        return this.forward(1)
    }

    /**
     * 更新TF卡流只解码i帧播放倍率，支持playback()之前调用。
     * @param rate
     */
    updatePlaybackForwardMaxRateDecodeIFrame(rate) {
        rate = clamp(Number(rate), 1, 8);
        if (this.player) {
            this.player.updateOption({
                playbackForwardMaxRateDecodeIFrame: rate
            })
        } else {
            this._opt.playbackForwardMaxRateDecodeIFrame = rate
        }
    }


    setStreamQuality(quality) {
        if (!this.player) {
            return;
        }

        if (!this.player._opt.operateBtns.quality) {
            return;
        }

        const qualityList = this.player._opt.qualityConfig || [];
        if (qualityList.includes(quality)) {
            this.player.streamQuality = quality;
        }
    }

    /**
     *
     * @param timestamp
     */
    setPlaybackStartTime(timestamp) {
        const strLength = getStrLength(timestamp)
        if (!this.player) {
            return;
        }
        if (strLength < 10) {
            return;
        }

        if (this.player.playing) {
            if (strLength === 10) {
                timestamp = timestamp * 1000
            }
            this.player.video && this.player.video.clear();
            this.player.audio && this.player.audio.clear();
            this.player.playback.setStartTime(timestamp);
            this.player.decoderWorker.clearWorkBuffer(true);
        }
    }


    /**
     *
     * @param url {string}
     * @returns {Promise<unknown>}
     * @private
     */
    _play(url, options = {}) {
        return new Promise((resolve, reject) => {
            this._opt.url = url;
            //  新的url
            const isHttp = url.indexOf("http") === 0;
            const isWebrtc = url.indexOf('webrtc') === 0;
            const isWebTransport = url.indexOf('wt') === 0;
            const isHls = !isWebrtc && url.indexOf('.m3u8') !== -1;
            const isFlv = !isWebrtc && url.indexOf(".flv") !== -1;

            //
            let protocol = null
            let demuxType = null;

            if (isHttp) {
                if (isHls) {
                    protocol = PLAYER_PLAY_PROTOCOL.hls;
                } else {
                    protocol = PLAYER_PLAY_PROTOCOL.fetch;
                }
            } else if (isWebTransport) {
                protocol = PLAYER_PLAY_PROTOCOL.webTransport;
            } else {
                if (isWebrtc) {
                    protocol = PLAYER_PLAY_PROTOCOL.webrtc;
                } else {
                    protocol = PLAYER_PLAY_PROTOCOL.websocket;
                }
            }

            if ((isHttp && !isHls) || isFlv || this._opt.isFlv) {
                demuxType = DEMUX_TYPE.flv
            } else {
                if (isHls) {
                    demuxType = DEMUX_TYPE.hls
                } else if (isWebrtc) {
                    demuxType = DEMUX_TYPE.webrtc
                } else if (isWebTransport) {
                    demuxType = DEMUX_TYPE.webTransport
                } else {
                    demuxType = DEMUX_TYPE.m7s
                }
            }

            if (isFlv && !this._opt.isFlv) {
                this._opt.isFlv = true;
            }

            if (!(protocol && demuxType)) {
                // this.player.debug.log('Jessibuca', `play protocol is ${protocol}, demuxType is ${demuxType}`)
                return reject(`play protocol is ${protocol}, demuxType is ${demuxType}`);
            }

            //

            if (isHls || isWebrtc) {
                this._resetPlayer({
                    protocol,
                    demuxType,
                    isHls,
                    isWebrtc,
                })
            } else {
                this.player.updateOption({
                    protocol,
                    demuxType,
                    isFlv: this._opt.isFlv,
                })
            }
            // webgl
            this.player.once(EVENTS_ERROR.webglAlignmentError, () => {
                this.pause().then(() => {
                    this.player.debug.log('Jessibuca', 'webglAlignmentError')
                    this._resetPlayer({openWebglAlignment: true})
                    this.play(url).then(() => {
                        // resolve();
                        this.player.debug.log('Jessibuca', 'webglAlignmentError and play success')
                    }).catch((e) => {
                        // reject();
                        this.player.debug.error('Jessibuca', 'webglAlignmentError and play error', e)
                    });
                });
            })
            // MSE
            this.player.once(EVENTS_ERROR.mediaSourceH265NotSupport, () => {
                this.pause().then(() => {
                    if (this.player._opt.autoWasm) {
                        this.player.debug.log('Jessibuca', 'auto wasm [mse-> wasm] reset player and play')
                        this._resetPlayer({useMSE: false})
                        this.play(url).then(() => {
                            // resolve();
                            this.player.debug.log('Jessibuca', 'auto wasm [mse-> wasm] reset player and play success')
                        }).catch((e) => {
                            // reject();
                            this.player.debug.error('Jessibuca', 'auto wasm [mse-> wasm] reset player and play error', e)
                        });
                    }
                });
            })
            // MSE
            this.player.once(EVENTS_ERROR.mediaSourceFull, () => {
                this.pause().then(() => {
                    this.player.debug.log('Jessibuca', 'media source full');
                    this._resetPlayer()
                    this.play(url).then(() => {
                        // resolve();
                        this.player.debug.log('Jessibuca', 'media source full and reset player and play success')
                    }).catch((e) => {
                        // reject();
                        this.player.debug.error('Jessibuca', 'media source full and reset player and play error', e)
                    });
                })
            })
            // MSE
            this.player.once(EVENTS_ERROR.mediaSourceAppendBufferError, () => {
                this.pause().then(() => {
                    this.player.debug.log('Jessibuca', 'media source append buffer error');
                    this._resetPlayer()
                    this.play(url).then(() => {
                        // resolve();
                        this.player.debug.log('Jessibuca', 'media source append buffer error and reset player and play success')
                    }).catch((e) => {
                        // reject();
                        this.player.debug.error('Jessibuca', 'media source append buffer error and reset player and play error', e)
                    });
                })
            })
            // MSE
            this.player.once(EVENTS_ERROR.mediaSourceBufferListLarge, () => {
                this.pause().then(() => {
                    this.player.debug.log('Jessibuca', 'media source buffer list large');
                    this._resetPlayer()
                    this.play(url).then(() => {
                        // resolve();
                        this.player.debug.log('Jessibuca', 'media source buffer list large and reset player and play success')
                    }).catch((e) => {
                        // reject();
                        this.player.debug.error('Jessibuca', 'media source buffer list large and reset player and play error', e)
                    });
                })
            })
            // MSE
            this.player.once(EVENTS_ERROR.mediaSourceAppendBufferEndTimeout, () => {
                this.pause().then(() => {
                    this.player.debug.log('Jessibuca', 'media source append buffer end timeout');
                    this._resetPlayer()
                    this.play(url).then(() => {
                        // resolve();
                        this.player.debug.log('Jessibuca', 'media source append buffer end timeout and reset player and play success')
                    }).catch((e) => {
                        // reject();
                        this.player.debug.error('Jessibuca', 'media source append buffer end timeout and reset player and play error', e);
                    });
                })
            })
            // MSE
            this.player.once(EVENTS_ERROR.mseSourceBufferError, () => {
                this.pause().then(() => {
                    if (this.player._opt.autoWasm) {
                        this.player.debug.log('Jessibuca', 'mseSourceBufferError auto wasm [mse-> wasm] reset player and play')
                        this._resetPlayer({useMSE: false})
                        this.play(url).then(() => {
                            // resolve();
                            this.player.debug.log('Jessibuca', 'mseSourceBufferError auto wasm [mse-> wasm] reset player and play success')
                        }).catch((e) => {
                            // reject();
                            this.player.debug.error('Jessibuca', 'mseSourceBufferError auto wasm [mse-> wasm] reset player and play error', e)
                        });
                    }
                })
            })
            // webcodecs
            this.player.once(EVENTS_ERROR.webcodecsH265NotSupport, () => {
                this.pause().then(() => {
                    if (this.player._opt.autoWasm) {
                        this.player.debug.log('Jessibuca', 'auto wasm [wcs-> wasm] reset player and play')
                        this._resetPlayer({useWCS: false})
                        this.play(url).then(() => {
                            // resolve();
                            this.player.debug.log('Jessibuca', 'auto wasm [wcs-> wasm] reset player and play success')
                        }).catch((e) => {
                            // reject();
                            this.player.debug.error('Jessibuca', 'auto wasm [wcs-> wasm] reset player and play error', e);
                        });
                    }
                });
            })
            // webcodecs
            this.player.once(EVENTS_ERROR.webcodecsWidthOrHeightChange, () => {
                this.pause().then(() => {
                    this.player.debug.log('Jessibuca', 'webcodecs Width Or Height Change reset player and play')
                    this._resetPlayer({useWCS: true})
                    this.play(url).then(() => {
                        // resolve();
                        this.player.debug.log('Jessibuca', 'webcodecs Width Or Height Change reset player and play success')
                    }).catch((e) => {
                        // reject();
                        this.player.debug.warn('Jessibuca', 'webcodecs Width Or Height Change reset player and play error', e);
                    });
                });
            })
            // webcodecs
            this.player.once(EVENTS_ERROR.webcodecsDecodeError, () => {
                this.pause().then(() => {
                    if (this.player._opt.autoWasm) {
                        this.player.debug.log('Jessibuca', 'webcodecs decode error reset player and play')
                        this._resetPlayer({useWCS: false})
                        this.play(url).then(() => {
                            // resolve();
                            this.player.debug.log('Jessibuca', 'webcodecs decode error  reset player and play success')
                        }).catch((e) => {
                            // reject();
                            this.player.debug.error('Jessibuca', 'webcodecs decode error reset player and play error', e)
                        });
                    }
                });
            })
            // wasm。
            this.player.once(EVENTS_ERROR.wasmDecodeError, () => {
                if (this.player._opt.wasmDecodeErrorReplay) {
                    this.pause().then(() => {
                        this.player.debug.log('Jessibuca', 'wasm decode error and reset player and play')
                        this.play(url, options).then(() => {
                            // resolve();
                            this.player.debug.log('Jessibuca', 'wasm decode error and reset player and play success')
                        }).catch((e) => {
                            // reject();
                            this.player.debug.error('Jessibuca', 'wasm decode error and reset player and play error', e)
                        });
                    })
                }
            })

            // 网络超时
            this.player.once(EVENTS.networkDelayTimeout, () => {
                if (this.player._opt.networkDelayTimeoutReplay) {
                    this.pause().then(() => {
                        this.player.debug.log('Jessibuca', `network delay time out and reset player and play`)
                        this.play(url, options).then(() => {
                            // resolve();
                            this.player.debug.log('Jessibuca', 'wasm decode error and reset player and play success')
                        }).catch((e) => {
                            // reject();
                            this.player.debug.error('Jessibuca', 'wasm decode error and reset player and play error', e)
                        });
                    })
                }

            })

            // 监听 delay timeout
            this.player.on(EVENTS.delayTimeout, () => {
                if (this.player._opt.heartTimeoutReplay && (this._heartTimeoutReplayTimes < this.player._opt.heartTimeoutReplayTimes || this.player._opt.heartTimeoutReplayTimes === -1)) {
                    this.player.debug.log('Jessibuca', `delay timeout replay time is ${this._heartTimeoutReplayTimes}`)
                    this._heartTimeoutReplayTimes += 1;
                    this.play(url, options).then(() => {
                        // resolve();
                        this._heartTimeoutReplayTimes = 0;
                    }).catch((e) => {
                        // reject();
                        this.player.debug.error('Jessibuca', 'delay timeout replay error', e)
                    });
                }
            })
            // 监听 loading timeout
            this.player.on(EVENTS.loadingTimeout, () => {
                if (this.player._opt.loadingTimeoutReplay && (this._loadingTimeoutReplayTimes < this.player._opt.loadingTimeoutReplayTimes || this.player._opt.loadingTimeoutReplayTimes === -1)) {
                    this.player.debug.log('Jessibuca', `loading timeout replay time is ${this._loadingTimeoutReplayTimes}`)
                    this._loadingTimeoutReplayTimes += 1;
                    this.play(url, options).then(() => {
                        // resolve();
                        this._loadingTimeoutReplayTimes = 0;
                    }).catch((e) => {
                        // reject();
                        this.player.debug.error('Jessibuca', 'loading timeout replay error', e)
                    });
                }
            })

            if (this.hasLoaded()) {
                this.player.play(url, options).then(() => {
                    resolve();
                }).catch((e) => {
                    this.player.debug.error('Jessibuca', 'hasLoaded and play error', e)
                    this.player.pause().then(() => {
                        reject();
                    })
                })
            } else {
                this.player.once(EVENTS.decoderWorkerInit, () => {
                    this.player.play(url, options).then(() => {
                        resolve();
                    }).catch((e) => {
                        this.player.debug.error('Jessibuca', 'decoderWorkerInit and play error', e)
                        this.player.pause().then(() => {
                            reject();
                        })
                    })
                })
            }
        })
    }

    /**
     *
     */
    resize() {
        this.player && this.player.resize();
    }

    /**
     *
     * @param time {number} s
     */
    setBufferTime(time) {
        time = Number(time)
        if (!this.player) {
            return;
        }
        if (time > 10) {
            console.warn(`Jessibuca buffer time is ${time} second, is too large, video will show blank screen until cache ${time} second buffer data`)
        }

        // s -> ms
        this.player.updateOption({
            videoBuffer: time * 1000
        })
        // update worker config
        this.player.decoderWorker && this.player.decoderWorker.updateWorkConfig({
            key: 'videoBuffer',
            value: time * 1000
        })
    }

    /**
     *
     * @param time
     */
    setBufferDelayTime(time) {
        time = Number(time)
        if (!this.player) {
            return;
        }
        if (time < 0.2) {
            console.warn(`Jessibuca buffer time delay is ${time} second, is too small`)
        }

        time = clamp(time, 0.2, 100)

        // s -> ms
        this.player.updateOption({
            videoBufferDelay: time * 1000
        })
        // update worker config
        this.player.decoderWorker && this.player.decoderWorker.updateWorkConfig({
            key: 'videoBufferDelay',
            value: time * 1000
        })
    }

    /**
     *
     * @param deg {number}
     */
    setRotate(deg) {
        deg = parseInt(deg, 10)
        const list = [0, 90, 180, 270];
        if (!this.player) {
            return;
        }
        if (this._opt.rotate === deg || list.indexOf(deg) === -1) {
            return;
        }
        this.player.updateOption({
            rotate: deg
        })
        this.resize();
    }

    /**
     * 设置镜像翻转
     * @param type
     */
    setMirrorRotate(mirrorRotate) {
        const list = ['none', 'level', 'vertical'];
        if (!this.player) {
            return;
        }
        if (this._opt.mirrorRotate === mirrorRotate || list.indexOf(mirrorRotate) === -1) {
            return;
        }
        this.player.updateOption({
            mirrorRotate
        })

        this.resize()
    }

    /**
     *
     * @returns {boolean}
     */
    hasLoaded() {
        return (this.player && this.player.loaded) || false;
    }

    /**
     *
     */
    setKeepScreenOn() {

        this.player && this.player.updateOption({
            keepScreenOn: true
        })
    }

    /**
     *
     * @param flag {Boolean}
     */
    setFullscreen(flag) {
        const fullscreen = !!flag;
        if (!this.player) {
            return
        }
        if (this.player.fullscreen !== fullscreen) {
            this.player.fullscreen = fullscreen;
        }
    }

    /**
     *
     * @param filename {string}
     * @param format {string}
     * @param quality {number}
     * @param type {string} download,base64,blob
     */
    screenshot(filename, format, quality, type) {
        if (this.player && this.player.video) {
            return this.player.video.screenshot(filename, format, quality, type)
        }
        return null;
    }

    /**
     *
     * @param options
     * @returns Promise
     */
    screenshotWatermark(options) {
        if (this.player && this.player.video) {
            return this.player.video.screenshotWatermark(options)
        }
        return null;
    }

    /**
     *
     * @param fileName {string}
     * @param fileType {string}
     * @returns {Promise<unknown>}
     */
    startRecord(fileName, fileType) {
        return new Promise((resolve, reject) => {
            if (!this.player) {
                return reject();
            }
            if (this.player.playing) {
                this.player.startRecord(fileName, fileType)
                resolve();
            } else {
                reject();
            }
        })
    }

    stopRecordAndSave() {
        if (this.player && this.player.recording) {
            this.player.stopRecordAndSave();
        }
    }

    /**
     *
     * @returns {Boolean}
     */
    isPlaying() {
        return this.player ? this.player.playing : false;
    }

    /**
     * 是否静音状态
     * @returns {Boolean}
     */
    isMute() {
        return (this.player && this.player.audio) ? this.player.audio.isMute : true;
    }

    /**
     * 是否在录制视频
     * @returns {*}
     */
    isRecording() {
        return (this.player && this.player.recorder) && this.player.recorder.recording || false;
    }

    /**
     * 清除延迟
     */
    clearBufferDelay() {
        if (this.player) {
            this.player.clearBufferDelay();
        }
    }

    /**
     * 获取解码方式
     */
    getDecodeType() {
        let result = '';
        if (this.player) {
            const options = this.player.getOption();
            if (options.useMSE) {
                result += DECODE_TYPE.mse + ' '
            }
            if (options.useWCS) {
                result += DECODE_TYPE.wcs + ' '
            }
            if (options.useWasm) {
                result += DECODE_TYPE.wasm + ' '
            }
            if (options.useSIMD) {
                result += DECODE_TYPE.simd + ' '
            }
            if (options.useOffscreen) {
                result += DECODE_TYPE.offscreen + ' '
            }
        }
        return result;
    }

    getRenderType() {
        let result = '';

        if (this.player) {
            result = this.player.getRenderType();
        }

        return result;
    }

    /**
     * get playing timestamp
     * @returns {number}
     */
    getPlayingTimestamp() {
        let result = 0;
        if (this.player) {
            result = this.player.getPlayingTimestamp();
        }
        return result;
    }

    /**
     * get player now status
     * @returns {string}
     */
    getStatus() {
        let result = PLAYER_STATUS.destroy;

        if (this.player) {
            if (this.player.loading) {
                result = PLAYER_STATUS.loading
            } else {
                if (this.player.playing) {
                    result = PLAYER_STATUS.playing
                } else {
                    result = PLAYER_STATUS.pause
                }
            }
        }
        return result;
    }

    getPlayType() {
        return this.player ? this.player._opt.playType : PLAY_TYPE.player;
    }
}


window.JessibucaPro = JessibucaPro;

export default JessibucaPro;
