import CommonContextLoader from "./commonContextLoader";
import {AUDIO_SYNC_VIDEO_DIFF, EVENTS} from "../constant";
import Processor from "./processor";
import RateProcessor from "./rateProcessor";

export default class AudioPlaybackLoader extends CommonContextLoader {
    constructor(player) {
        super(player);

        //
        this.playbackRate = 1;
        this.rateProcessor = null;
        this.processor = null;
        this.firstTimestamp = null;

        this.player.debug.log('AudioPlaybackContext', 'init');

    }

    destroy() {
        super.destroy();
        if (this.processor) {
            this.processor.destroy();
            this.processor = null;
        }
        if (this.rateProcessor) {
            this.rateProcessor.destroy();
            this.rateProcessor = null;
        }
        this.player.debug.log(`AudioPlaybackLoader`, 'destroy');

    }


    initScriptNode(msg) {
        this.playing = true;
        if (this.hasInitScriptNode) {
            return;
        }

        const channels = this.audioInfo.channels;
        this.processor = new Processor(this.player, this, this.bufferList, this.audioContextChannel)
        this.rateProcessor = new RateProcessor(this.player, this, this.processor);


        const scriptNode = this.audioContext.createScriptProcessor(1024, 0, channels);
        // tips: if audio isStateSuspended  onaudioprocess method not working
        scriptNode.onaudioprocess = (audioProcessingEvent) => {
            const outputBuffer = audioProcessingEvent.outputBuffer;
            //
            if (this.bufferList.length && this.playing) {
                // just for wasm
                // audio > video
                // wait
                if (this.audioSyncVideoOption.diff > AUDIO_SYNC_VIDEO_DIFF) {
                    // this.player.debug.warn('AudioContext', `audioSyncVideoOption more than diff :${this.audioSyncVideoOption.diff}, waiting`)
                    // wait
                    return;
                }
                    // audio < video
                // throw away then chase video
                else if (this.audioSyncVideoOption.diff < -AUDIO_SYNC_VIDEO_DIFF) {
                    // this.player.debug.warn('AudioContext', `audioSyncVideoOption less than diff :${this.audioSyncVideoOption.diff}, dropping`)

                    //
                    let bufferItem = this._provide(outputBuffer.length);
                    //
                    while ((bufferItem.ts - this.player.videoTimestamp < -AUDIO_SYNC_VIDEO_DIFF) && bufferItem.size > 0) {
                        // this.player.debug.warn('AudioContext', `audioSyncVideoOption less than inner ts is:${bufferItem.ts}, videoTimestamp is ${this.player.videoTimestamp},diff:${bufferItem.ts - this.player.videoTimestamp}`)
                        bufferItem = this._provide(outputBuffer.length);
                    }

                    if (bufferItem.size === 0) {
                        return;
                    }
                }

                const bufferItem = this._provide(outputBuffer.length);

                if (bufferItem.size === 0) {
                    return;
                }

                // update audio time stamp
                if (bufferItem && bufferItem.ts) {
                    this.player.audioTimestamp = bufferItem.ts;
                }

                for (let channel = 0; channel < channels; channel++) {
                    const b = bufferItem.buffer && bufferItem.buffer[channel]
                    const nowBuffering = outputBuffer.getChannelData(channel);
                    for (let i = 0; i < 1024; i++) {
                        nowBuffering[i] = (b && b[i]) || 0
                    }
                }
            }
        }

        scriptNode.connect(this.gainNode);
        this.scriptNode = scriptNode;
        this.gainNode.connect(this.audioContext.destination);
        this.gainNode.connect(this.mediaStreamAudioDestinationNode);
        this.hasInitScriptNode = true;
    }

    //
    initScriptNodeDelay(msg) {
        const delayTime = this.player._opt.playbackDelayTime
        if (delayTime > 0) {
            setTimeout(() => {
                this.initScriptNode(msg);
                // console.error('initScriptNodeDelay , bufferList length', this.bufferList.length);
            }, delayTime)
        } else {
            this.initScriptNode(msg);
        }
    }


    setRate(value) {
        if (value !== this.playbackRate && this.rateProcessor) {
            this.player.debug.log('AudioPlaybackContext', 'setRate', value);
            this.playbackRate = value;
            this.rateProcessor.setRate(value);
        }
    }

    play(buffer, ts) {
        // if is mute
        if (this.isMute) {
            return;
        }

        if (this.firstTimestamp === null) {
            this.firstTimestamp = ts;
        }

        this.hasAudio = true;

        this.bufferList.push({
            buffer,
            ts: ts - this.firstTimestamp
        });
    }

    _provide(size) {
        let provider = this.playbackRate === 1 ? this.processor : this.rateProcessor;
        let audioData = provider.provide(size);
        return audioData;
    }
}
