import Emitter from "../utils/emitter";
import {getWebRtcRemoteSdp} from "../utils/webrtc";

export default class WebrtcDecoder extends Emitter {
    constructor(player) {
        super();
        this.player = player;
        this.rtcPeerConnection = null;
        this.videoStream = null;
        this._initRtcPeerConnection();
        this.player.debug.log('WebrtcDecoder', 'init');
    }

    destroy() {
        if (this.rtcPeerConnection) {
            this.rtcPeerConnection.close();
            this.rtcPeerConnection = null;
        }
        this.videoStream = null;
        this.player.video.$videoElement.srcObject = null;
        this.player.debug.log('WebrtcDecoder', 'destroy');
    }


    _initRtcPeerConnection() {
        const rtcPeerConnection = new RTCPeerConnection();
        const player = this.player;
        rtcPeerConnection.addTransceiver("video", {
            direction: "recvonly",
        });
        rtcPeerConnection.addTransceiver("audio", {
            direction: "recvonly",
        });

        rtcPeerConnection.onsignalingstatechange = (e) => {
            console.log("onsignalingstatechange", e);
        };

        rtcPeerConnection.oniceconnectionstatechange = (e) => {
            console.log(
                "oniceconnectionstatechange",
                rtcPeerConnection.iceConnectionState
            );
        };

        rtcPeerConnection.onicecandidate = (event) => {
            console.log("onicecandidate", event);
        };

        rtcPeerConnection.ontrack = (event) => {
            const $video = player.video.$videoElement;
            console.log("ontrack", event);

            if (event.track.kind === 'video') {
                let videoStream = event.streams[0];
                $video.srcObject = videoStream;
                this.videoStream = videoStream;
            }
        }

        rtcPeerConnection.onconnectionstatechange = (event) => {
            player.debug.log('webrtcLoader', `sdp connect status ${rtcPeerConnection.connectionState}`);
            switch (rtcPeerConnection.connectionState) {
                case "connected":
                    break;
                case "disconnected":
                case "failed":
                    // player.emit(EVEMTS.streamError)
                    break;
                case "closed":
                    // player.emit(EVEMTS.streamEnd)
                    break;
            }
        }


        this.rtcPeerConnection = rtcPeerConnection;
    }


    loadSource(url) {
        return new Promise((resolve, reject) => {
            const rtcPeerConnection = this.rtcPeerConnection;
            rtcPeerConnection.createOffer().then((res) => {
                rtcPeerConnection.setLocalDescription(res);
                this.player.debug.log('webrtcLoader', `getWebRtcRemoteSdp loadSource`);
                getWebRtcRemoteSdp(url, res.sdp).then((response) => {
                    response.text().then((sdp) => {
                        this.player.debug.log('webrtcLoader', `getWebRtcRemoteSdp response`);
                        rtcPeerConnection.setRemoteDescription(
                            new RTCSessionDescription({
                                type: "answer",
                                sdp
                            })
                        )
                        resolve()
                    }).catch((e) => {
                        this.player.debug.error('webrtcLoader', `loadSource response.text() error`, e);
                        console.error(e);
                        reject()
                    })
                }).catch((e) => {
                    this.player.debug.error('webrtcLoader', `loadSource getWebRtcRemoteSdp response error`, e);
                    reject(e);
                })
            }).catch((e) => {
                this.player.debug.error('webrtcLoader', `loadSource rtcPeerConnection.createOffer() error`, e);
                reject(e);
            })
        })
    }
}


