const iconsMap = {
    play: '播放',
    pause: '暂停',
    audio: '',
    mute: '',
    screenshot: '截图',
    loading: '加载',
    fullscreen: '全屏',
    fullscreenExit: '退出全屏',
    record: '录制',
    recordStop: '停止录制',
    narrow: '缩小',
    expand: '放大',
    ptz: '操作盘',
    ptzActive: '操作盘激活',
    zoom: '电子放大',
    zoomStop: '关闭电子放大',
    close: '关闭'
}

export default Object.keys(iconsMap).reduce((icons, key) => {
    icons[key] = `
    <i class="jessibuca-icon jessibuca-icon-${key}"></i>
    ${iconsMap[key] ? `<span class="icon-title-tips"><span class="icon-title">${iconsMap[key]}</span></span>` : ''}
`;
    return icons;
}, {});
