import Emitter from "../utils/emitter";
import {
    DOCUMENT_HIDDEN_MAX_BUFFER_LENGTH,
    DOCUMENT_HIDDEN_MIN_BUFFER_LENGTH,
    EVENTS,
    MEDIA_TYPE,
    PLAY_TYPE
} from "../constant";
import {now} from "../utils";

export default class CommonLoader extends Emitter {
    constructor(player) {
        super();
        this.player = player;

        this.stopId = null;
        this.firstTimestamp = null;
        this.startTimestamp = null;
        this.bufferStartDts = null;
        this.bufferStartLocalTs = null;

        this.delay = -1;
        this.bufferList = [];
        this.dropping = false;
        this.networkDelayTimeoutLock = false;
        this.initInterval();
    }

    destroy() {
        this.bufferList = [];
        if (this.stopId) {
            clearInterval(this.stopId);
            this.stopId = null;
        }
        this.firstTimestamp = null;
        this.startTimestamp = null;
        this.bufferStartDts = null;
        this.bufferStartLocalTs = null;
        this.delay = -1;
        this.dropping = false;
        this.off();
        this.player.debug.log('CommonDemux', 'destroy');
    }

    getDelay(timestamp) {
        if (!timestamp) {
            return -1
        }
        if (!this.firstTimestamp) {
            this.firstTimestamp = timestamp
            this.startTimestamp = Date.now()
            this.delay = -1;
        } else {
            if (timestamp) {
                const localTimestamp = (Date.now() - this.startTimestamp);
                const timeTimestamp = (timestamp - this.firstTimestamp);
                // console.error(`localTimestamp is ${localTimestamp}, timeTimestamp is ${timeTimestamp}`)
                if (localTimestamp >= timeTimestamp) {
                    this.delay = localTimestamp - timeTimestamp;
                } else {
                    // maybe some stream ts more large than local ts
                    this.delay = timeTimestamp - localTimestamp;
                }
            }
        }
        return this.delay
    }

    resetDelay() {
        this.firstTimestamp = null;
        this.startTimestamp = null;
        this.delay = -1;
        this.dropping = false;
    }

    //
    initInterval() {
        this.player.debug.log('common dumex', `init Interval`);
        this._loop();
        this.stopId = setInterval(() => {
            this._loop()
        }, 10)
    }

    _loop() {
        let data;
        const videoBuffer = this.player._opt.videoBuffer;
        const videoBufferDelay = this.player._opt.videoBufferDelay;
        if (this.bufferList.length) {
            if (this.dropping) {
                // this.player.debug.log('common dumex', `is dropping`);
                data = this.bufferList.shift();
                if (data.type === MEDIA_TYPE.audio && data.payload[1] === 0) {
                    this._doDecoderDecode(data);
                }
                while (!data.isIFrame && this.bufferList.length) {
                    data = this.bufferList.shift();
                    if (data.type === MEDIA_TYPE.audio && data.payload[1] === 0) {
                        this._doDecoderDecode(data);
                    }

                }
                // i frame
                // min delay
                if (data.isIFrame && this.bufferList.length < DOCUMENT_HIDDEN_MIN_BUFFER_LENGTH) {
                    this.player.debug.warn('common dumex', `_loop data isIFrame is true and delay is ${this.delay}`);
                    this.dropping = false;
                    this._doDecoderDecode(data);
                }
            } else {
                data = this.bufferList[0];
                if (this.getDelay(data.ts) === -1) {
                    // this.player.debug.log('common dumex', `delay is -1`);
                    this.bufferList.shift()
                    this._doDecoderDecode(data);
                } else if (this.delay > (videoBufferDelay + videoBuffer)) {
                    // this.player.debug.log('common dumex', `delay is ${this.delay}, set dropping is true`);
                    this.resetDelay();
                    this.dropping = true
                } else {
                    if (this.delay > videoBuffer) {
                        // drop frame
                        this.bufferList.shift()
                        this._doDecoderDecode(data);
                        //this.player.debug.log('common dumex', `do decode`);
                    } else {
                        if (this.delay < 0) {
                            this.player.debug.error('common dumex', `delay is ${this.delay} bufferList is ${this.bufferList}`);
                        }
                    }
                }
            }
        }
    }

    _doDecode(payload, type, ts, isIFrame, cts) {
        // console.error('_doDecode', ts, cts);
        const player = this.player;
        let options = {
            ts: ts,
            cts: cts,
            type: type,
            isIFrame: false
        }
        if (type === MEDIA_TYPE.video) {
            this.calcNetworkDelay(ts);
        }

        // use offscreen
        if (player._opt.useWCS && !player._opt.useOffscreen) {
            if (type === MEDIA_TYPE.video) {
                options.isIFrame = isIFrame;
            }
            this.pushBuffer(payload, options)
        } else if (player._opt.useMSE) {
            // use mse
            if (type === MEDIA_TYPE.video) {
                options.isIFrame = isIFrame;
            }
            this.pushBuffer(payload, options)
        } else {
            //
            if (type === MEDIA_TYPE.video) {
                player.decoderWorker && player.decoderWorker.decodeVideo(payload, ts, isIFrame);
            } else if (type === MEDIA_TYPE.audio) {
                if (player._opt.hasAudio) {
                    player.decoderWorker && player.decoderWorker.decodeAudio(payload, ts);
                }
            }
        }
    }

    _doDecoderDecode(data) {
        const player = this.player;
        const {webcodecsDecoder, mseDecoder} = player;

        if (data.type === MEDIA_TYPE.audio) {
            if (player._opt.hasAudio) {
                player.decoderWorker && player.decoderWorker.decodeAudio(data.payload, data.ts)
            }
        } else if (data.type === MEDIA_TYPE.video) {
            if (player._opt.useWCS && !player._opt.useOffscreen) {
                webcodecsDecoder.decodeVideo(data.payload, data.ts, data.isIFrame);
            } else if (player._opt.useMSE) {
                mseDecoder.decodeVideo(data.payload, data.ts, data.isIFrame, data.cts);
            }
        }
    }

    pushBuffer(payload, options) {

        const videoBuffer = this.player._opt.videoBuffer;
        const videoBufferDelay = this.player._opt.videoBufferDelay;
        // console.warn('pushBuffer', `ts is ${options.ts}, delay is ${this.getDelay(options.ts)},max delay is ${videoBufferDelay + videoBuffer}`)
        if (this.getDelay(options.ts) > (videoBufferDelay + videoBuffer)) {
            this.dropBuffer();
        }

        if (this.player._opt.useMSE) {
            const sourceBufferDelay = this.player.mseDecoder.checkSourceBufferDelay();
            if (sourceBufferDelay * 1000 > (videoBufferDelay + videoBuffer)) {
                // console.error('sourceBufferDelay: ', sourceBufferDelay);
                this.player.mseDecoder.dropSourceBuffer(true);
            }
        }

        // 音频
        if (options.type === MEDIA_TYPE.audio) {
            this.bufferList.push({
                ts: options.ts,
                payload: payload,
                type: MEDIA_TYPE.audio,
            })
        } else if (options.type === MEDIA_TYPE.video) {
            this.bufferList.push({
                ts: options.ts,
                cts: options.cts,
                payload: payload,
                type: MEDIA_TYPE.video,
                isIFrame: options.isIFrame
            })
        }

        if (this.bufferList.length > DOCUMENT_HIDDEN_MAX_BUFFER_LENGTH) {
            this.player.debug.warn('CommonDemux', `bufferList is ${this.bufferList.length}`);
            this.dropBuffer();
        }
    }

    dropBuffer() {

        while (this.bufferList.length) {
            let data = this.bufferList[0];
            if (data.type === MEDIA_TYPE.audio && data.payload[1] === 0) {
                this._doDecoderDecode(data);
                this.bufferList.shift();
            }
            // i frame
            // min delay
            if (data.isIFrame && this.bufferList.length < DOCUMENT_HIDDEN_MIN_BUFFER_LENGTH) {
                this.player.debug.warn('common dumex', `dropBuffer end isIFrame ${data.isIFrame} and delay is ${this.delay} and bufferlist is ${this.bufferList.length}`);
                break;
            }
            this.player.debug.log('common dumex', `is dropping and isIFrame ${data.isIFrame} and delay is ${this.delay} and bufferlist is ${this.bufferList.length}`);
            this.bufferList.shift();
        }

        if (this.player._opt.useMSE) {
            this.player.mseDecoder.dropSourceBuffer(true)
        }
    }

    clearBuffer(needClear = false) {
        this.player.debug.log('CommonDemux', `clearBuffer,buffer length is ${this.bufferList.length}, need clear is ${needClear}`)
        if (needClear) {
            this.bufferList = [];
        }
        this.resetDelay();
        this.dropping = true;
    }

    calcNetworkDelay(dts) {
        if (dts <= 0) {
            return;
        }
        if (this.bufferStartDts === null) {
            this.bufferStartDts = dts;
            this.bufferStartLocalTs = now()
        }
        const diff1 = dts - this.bufferStartDts;
        const localDiff = now() - this.bufferStartLocalTs;
        const delay = localDiff > diff1 ? localDiff - diff1 : diff1 - localDiff;
        // just for
        if (delay > this.player._opt.networkDelay && this.player._opt.playType === PLAY_TYPE.player) {
            this.player.debug.warn('CommonDemux', `demuxFlv now dts:${dts},start dts is ${this.bufferStartDts}, vs start is ${diff1},local diff is ${localDiff} ,delay is ${delay}`);
            this.player.emit(EVENTS.networkDelayTimeout, delay)
        }
        this.player.updateStats({
            netBuf: delay
        })
    }

    resetNetworkDelayTimeoutLock() {
        this.networkDelayTimeoutLock = false;
    }

    close() {

    }

    reset() {

    }
}
