import {clamp, getLocationSearch, getStrLength} from "./utils";
import './preview.scss';
import Player from './player';
import Events from "./utils/events";
import {
    DECODE_TYPE,
    DEFAULT_JESSIBUCA_OPTIONS,
    DEMUX_TYPE,
    EVENTS,
    EVENTS_ERROR,
    JESSIBUCA_EVENTS, PLAY_TYPE,
    PLAYER_PLAY_PROTOCOL, PLAYER_STATUS,
    SCALE_MODE_TYPE
} from "./constant";
import {isEmpty, isNotEmpty, supportWCS, uuid16} from "./utils";
import Emitter from "./utils/emitter";
import Debug from "./utils/debug";
import JessibucaPro from './jessibuca';

function _getStreamPath() {
    const pathName = window.location.pathname;
    return pathName.replace('/preview/', '')
}


function _isLocalhost() {
    return window.location.hostname === 'localhost' || 'monibuca.com';
}


const isHttp = window.location.protocol === 'http:'
const wsProtocol = isHttp ? 'ws:' : 'wss:'
let port = window.location.port;
if (port !== '') {
    port = ':' + port
}
const hostName = window.location.hostname;
const streamPath = _getStreamPath();
const search = getLocationSearch();
const typeList = ['hdl', 'hls', 'ws', 'wt', 'rtc']
const localhost = `${window.location.protocol}//${hostName}${port}`;
const wsHost = `${wsProtocol}//${hostName}${port}`;
const wtPort = window.wtPort || '4433';
const wtHost = `wt://${hostName}:${wtPort}`;
const rtcHost = `webrtc://${hostName}${port}`
const searchType = search.type || typeList[0];
const playUrl = {
    hdl: localhost + '/hdl/${streamPath}.flv',
    hls: localhost + '/hls/${streamPath}.m3u8',
    ws: wsHost + '/jessica/${streamPath}',
    wt: wtHost + '/play/${streamPath}',
    rtc: rtcHost + '/webrtc/play/${streamPath}'
}
const typeObj = {
    hdl: 'Http-flv',
    hls: 'Hls',
    ws: 'Websocket',
    wt: 'WebTransport',
    rtc: 'Webrtc'
}

const createPlayerDom = () => {
    let optionsList = '';
    typeList.forEach((type) => {
        optionsList += `
            <option value="${type}" ${searchType === type ? 'selected' : ''}>${typeObj[type]}</option>
        `
    })
    const dom = `
            <div class="jessibuca-pro-wrap"></div>
            <div class="jessibuca-pro-btns">
                <select id="typeSelect">
                   ${optionsList}
                </select>
                <button id="changeType">切换</button>
            </div>
            `
    document.body.insertAdjacentHTML('beforeend', dom)
}

const bindEvents = () => {
    const $changeType = document.querySelector('#changeType');
    $changeType.addEventListener('click', () => {
        const $select = document.querySelector('#typeSelect');
        if ($select.value && $select.value !== searchType) {
            // const localHref = window.location.href;
            // if (window.location.search) {
            //     if (window.location.search.indexOf("&type=") !== -1) {
            //         window.location.href = localHref.replace(searchType, $select.value);
            //     } else {
            //         window.location.href = localHref + '&type=' + $select.value
            //     }
            // } else {
            //     window.location.href = localHref + '?type=' + $select.value
            // }
            window.location.href = localhost + '/preview/' + streamPath + '?type=' + $select.value
        }
    }, false)
}
//
const updateWindowTitle = () => {
    const type = typeObj[searchType];
    if (type) {
        window.document.title = `${type} Demo`
    }
}

if (_isLocalhost()) {
    if (searchType) {
        if (streamPath && typeList.includes(searchType)) {
            let url = playUrl[searchType];
            if (url) {
                url = url.replace('${streamPath}', streamPath);
                createPlayerDom();
                bindEvents();
                updateWindowTitle();
                const $container = document.querySelector('.jessibuca-pro-wrap');
                const jessibuca = new JessibucaPro({
                    container: $container,
                    showBandwidth: true, // 显示网速
                    // debug: search.debug === 'true',
                    debug:true,
                    // useMSE: true,
                    // wcsUseVideoRender: true,
                    operateBtns: {
                        fullscreen: true,
                        play: true,
                        audio: true,
                        close: true,
                    },
                    videoBuffer: 0.1,
                    isNotMute: true,
                })
                jessibuca.play(url).then(() => {
                    console.log('play success')
                }).catch((e) => {
                    console.error('play error', e)
                });
            }
        }

    } else {
        // filepath
    }
}

